@import url('https://fonts.googleapis.com/css?family=PT+Sans');


#background-img {
  position:absolute;
  background-image: url("img/sunny.jpg");
  background-repeat:no-repeat;
  background-size:cover;
  height: 100%;
  width: 100%;
  z-index: -1;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}


.app-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack:justify;
      justify-content:space-between;
  -ms-flex-line-pack:start;
      align-content:flex-start;
}




html, body, #root, .stretch-full {
  text-align: center;
  margin: 0;
  height: 100vh;
  width: 100vw;
  font-family: 'PT Sans', sans-serif;
}

span {
  white-space: pre;
}

.info{
  padding-left: 1%;
  padding-top: 1%;
  padding-bottom: 1.5%;
  text-align:left;
  color: black;
  mix-blend-mode:lighten;
}

#title {
  text-align:left;
  margin:0;
  font-weight: normal;
  padding-bottom: .25%;
}

#title > span {
  background: black;
  color: white;
  white-space:pre;
}


.header-main-section-footer{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
}



main {
  background: white;
  //background: #ecffb4 ;
  margin: auto;
  border-style: outset;
  border-color: black;
  border-width: .1em;
  width:80%;
  mix-blend-mode: lighten;
  outline: 1px solid black;
  outline-offset: -8px;
}



header h1 > span, .year span {
  border-style:solid;
  border-color:black;
  border-width: 1.5px;
  background: #e1ff869e;;
}



/* line-on-sides soln from stack overflow courtesy of electrophanteau
    Editted slightly to add size-change responsiveness
*/
.decorated{
     overflow: hidden;
     text-align: center;
 }
.decorated > span{
    position: relative;
    display: inline-block;
}
.decorated > span:before, .decorated > span:after{
    content: '';
    position: absolute;
    top: 50%;
    border-bottom: 1.5px solid;
    width: 15vw; /* half of limiter */
    margin: 0 20px;
}
.decorated > span:before{
    right: 100%;
}
.decorated > span:after{
    left: 100%;
}

.yearGroup > .decorated >span:before, .yearGroup >.decorated >span:after {
  border-bottom: 1px solid;
  color:black;
}
/* end of line-on-sides */

.hug{
  width: auto;
  display: inline-block;
}

.buttons {
  padding:.5%;
  padding-bottom: 1.5%;
}

button {
  background-color: #e1ff869e;
  font-size: 15pt;
  color: black;
  border: 1px solid black;
  cursor: pointer;
}

button:hover{
  background-color: black;
  color: beige;
}


.editor {
