  margin-left:auto;
  margin-right:auto;
}

.info span > span {
  font-size: 16pt;
}

.info > span {
  background: white;
  border-style:solid;
  border-color:black;
  border-width: 2px;
  padding: .15%;
  padding-top: .30%;
}

.year > span {
 border-style: solid;
 border-color: black;

}


.yearGroup {
  padding: 2%;
  padding-top: 0px;
}

.monthGroup {
  /*background-color: #e1ff869e; */
  background: white;
  margin-left: 2.5%;
  margin-right: 2.5%;
  border-style: solid;
